import posed, { PoseGroup } from "react-pose";

const firstAnimtaion = {
  visible: {
    opacity: 1,
    transition: { ease: "linear", duration: 300 },
    delay: 300
  },
  hidden: { opacity: 0 }
};

const secondAnimtaion = {
  visible: {
    opacity: 1,
    transition: { ease: "linear", duration: 300 },
    delay: 800
  },
  hidden: { opacity: 0 }
};

export const TextBlockAnimation = posed.div(firstAnimtaion);

export const ImageBgAnimation = posed.img(firstAnimtaion);

export const ImageFrontAnimation = posed.img(secondAnimtaion);

export const ImageFrontAnimationWiFi = posed.img({
  visible: {
    width: "auto",
    transition: { ease: "linear", duration: 300 },
    delay: 800
  },
  hidden: { width: 0 }
});
