import React, { useState } from "react";

import { Motion, spring } from "react-motion";

import Logo from "./Logo";
import {
  Header,
  Breadcrumbs,
  ButtonCircle,
  Button,
  ButtonOutline
} from "./styled";
import MenuBtn from "./MenuBtn";

const Nav = props => {
  const [btnActive, setBtnActive] = useState(false);

  // const handleClick

  return (
    <Header id="header">
      <Logo rotateDeg={props.logoRotateDeg} />
      <Breadcrumbs>ФИТНЕС клуб премиум Класса</Breadcrumbs>

      {btnActive === false && (
        <ButtonCircle onMouseEnter={() => setBtnActive(true)}>
          <i className="icon-phone" />
        </ButtonCircle>
      )}

      {btnActive && (
        <Motion
          defaultStyle={{ width: 0 }}
          style={{ width: spring(144, { stiffness: 200 }) }}
        >
          {interpolatingStyle => (
            <Button
              style={interpolatingStyle}
              onMouseLeave={() => setBtnActive(false)}
            >
              <a href={`tel:+7 (4212) 99-99-99`}>+7 (4212) 99-99-99</a>
            </Button>
          )}
        </Motion>
      )}

      <Button mLeft="20px">Заморозить карту</Button>
      <ButtonOutline mLeft="20px">Вступить в клуб</ButtonOutline>
      <MenuBtn />
    </Header>
  );
};

export default Nav;
